div.app {
	display: flex;
  flex-direction: column;
  min-height: 100vh;
	width: 90%;
	margin: 0 auto;
  color: #222;
}

header {
  flex-basis: 30vh;
  background-color: #fff;
}

main {
  display: flex;
  flex-grow: 1;
  padding: 30px 0;
}

aside {
  min-width: 180px;
}

section.page {
  flex-grow: 1;
}

footer {
  background-color: #000;
  color: #fff;
  padding: 10px 0;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 3px;
}